/* BUTTONS */
  
  #buttons {
    position: absolute;
    bottom: 10px;
    width: 85%;
    margin: 0 7.5%;
  }
  
  #buttons div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    background-color: #FAFAFA;
    -webkit-box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
            box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
    border-radius: 100%;
    width: 75px;
    height: 75px;
    z-index: 3;
  }
  
  #buttons div img {
    margin-top: 7.5px;
    width: 60px;
    height: 60px;
    opacity: 0.6;
  }
  
  #footer div p {
    color: grey;
    font-size: 18pt;
  }


